@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.sidebar {
  margin-top: 5px;
  display: grid;
  grid-template-rows: 1fr 3fr;
  grid-row-gap: 2px;
  /* padding: 20px; */
  background-color: #66c8d4;
}

.status-container {
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 15px;
}

hr {
  height: 5px;
  width: 100%;
  background-color: #171c28;
  border: none;
}
.btn {
  /* width: 100%; */
  background-color: #66c8d4 !important;
  border-radius: 5px;
  color: #fff;
}

.clock-in {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  margin-top: 0px;
  margin-bottom: 10px;
}

.clock-in label {
  font-size: 30px;
  color: #ce4ad8;
}

.clock-in button {
  border-radius: 50%;
  /* padding: 5px; */
  width: 50px;
  height: 50px;
  background: #ce4ad8 !important;
  border: none;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.clock-in button:hover {
  cursor: pointer;
  background-color: #66c8d4;
}

.clock-in-button {
  text-align: center;
}

.status-container i {
  font-size: 30px;
  padding: 5px;
  color: #292d39;
}

.social-content {
  background-color: #292d39;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  box-shadow: 2px 0px 10px 0 rgba(0, 0, 0, 0.14);
  color: #fff;
}

.status {
  background-color: #292d39;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 2px 0px 10px 0 rgba(0, 0, 0, 0.14);
  color: #fff;
}

.status p:nth-child(2) {
  margin-bottom: 5px;
  align-self: left;
}

.side-nav-open {
  position: absolute !important;
}

.nav-custom {
  /* margin: 0; */
  background-color: #292d39 !important;
  border-radius: 0px 0 5px 0px;
}

.nav-custom-wrapper {
  padding: 0 20px;
}

.nav-links {
  font-size: 20px;
}

.link-1:hover {
  background-color: #ce4ad8;
}
.link-2:hover {
  background-color: #d46b35;
}
.link-3:hover {
  background-color: #3fb485;
}
.link-4:hover {
  background-color: #66c8d4;
}

.mobile-nav {
  background-color: #292d39 !important;
}

.mobile-nav li a {
  background-image: -webkit-gradient(linear, left top, right top, from(#2edbaf), to(#57e2e5)) !important;
  background-image: -webkit-linear-gradient(left, #2edbaf, #57e2e5) !important;
  background-image: linear-gradient(to right, #2edbaf, #57e2e5) !important;
  margin: 20px 0;
  border-radius: 5px;
  font-size: 20px !important;
  color: #292d39 !important;
}

.nav-links-li {
  padding: 0 20px;
  border-radius: 5px !important;
  margin: 20px 0;
  text-align: center;
}
/* li:hover {
  background-color: #39b6ff;
} */

.nav-custom-wrapper img {
  margin-top: 7px;
  margin-left: 10px;
  width: 50%;
}
a.brand-logo {
  display: flex;
  justify-content: start;
  align-items: center;
}

.loged-in-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background-color: #292d39;
  padding: 0 10px;
  border-radius: 0px 0 0px 5px;
  box-shadow: 2px 0px 10px 0 rgba(0, 0, 0, 0.14);
}

.loged-in-nav .btn {
  margin: 0 10px;
  border-radius: 5px;
  /* background-image: linear-gradient(to right, #66c8d4, #39b6ff) !important; */
  background-color: #39b6ff;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .nav-custom-wrapper img {
    width: 70%;
    margin-top: 4px;
    margin-left: 0px;
  }

  a.brand-logo {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

