  display: flex;
  justify-content: space-between;
  width: 100%;
}

.first-name {
  margin-right: 30px;
}

.signup-form button {
  margin-top: 20px;
  align-self: center;
  background-image: -webkit-gradient(linear, left top, right top, from(#2edbaf), to(#57e2e5)) !important;
  background-image: -webkit-linear-gradient(left, #2edbaf, #57e2e5) !important;
  background-image: linear-gradient(to right, #2edbaf, #57e2e5) !important;
  border-radius: 5px !important;
}

.sign-up {
  background-image: url("/images/green-blue-background.svg");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  padding: 20px;
  min-height: 100vh;
  padding-top: 50px;
}

