#modal1 button {
  margin-top: 20px;
  background-image: -webkit-gradient(linear, left top, right top, from(#2edbaf), to(#57e2e5));
  background-image: -webkit-linear-gradient(left, #2edbaf, #57e2e5);
  background-image: linear-gradient(to right, #2edbaf, #57e2e5);
